import React from 'react'
import "./WhatsappButton.css"
const WhatsappButton = () => {
    const phoneNumber = '3547577756'; 
    const message = 'Hola, necesito más información sobre los alojamientos'; 
    const whatsappLink = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
    const handleButtonClick = () => {
        window.open(whatsappLink, '_blank', 'noopener,noreferrer');
      };
  return (
    <div className='containerAncorWpp'>
        <button onClick={handleButtonClick} rel="noopener noreferrer" className='ancorWpp' >
            <svg xmlns="http://www.w3.org/2000/svg" className="icon icon-tabler icon-tabler-brand-whatsapp btnWha" viewBox="0 0 24 24" stroke-width="2" stroke="#00b341" fill="none" stroke-linecap="round" stroke-linejoin="round">
                            <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                            <path d="M3 21l1.65 -3.8a9 9 0 1 1 3.4 2.9l-5.05 .9" />
                            <path d="M9 10a.5 .5 0 0 0 1 0v-1a.5 .5 0 0 0 -1 0v1a5 5 0 0 0 5 5h1a.5 .5 0 0 0 0 -1h-1a.5 .5 0 0 0 0 1" />
            </svg>
        </button>
    </div>
    
  )
}

export default WhatsappButton
